import { Component, ViewChild, AfterViewInit, ElementRef, OnDestroy } from '@angular/core';
import { ViewController, NavParams, NavOptions } from 'ionic-angular';

import { ImageCacher } from '../../experiment/imageCacher';


@Component({
	selector: 'page-splash',
	templateUrl: 'splash.html',
})
@ImageCacher([
	'GameHomePage'
])
export class SplashPage implements AfterViewInit, OnDestroy {

	@ViewChild('canvas') private _canvas : ElementRef;
	@ViewChild('loadingBar') private loadingBar : ElementRef;

	private canvas : HTMLCanvasElement;
	private context : CanvasRenderingContext2D;
	private loadingBarPercentage = 0;

	private coins = [];
	private images = [
		(()=>{
			//dollar
			let image = new Image();
			image.src = 'data:image/png;base64,'
			return image;
		})(),
		(()=>{
			//euro
			let image = new Image();
			image.src = 'data:image/png;base64,';
			return image;
		})(),
		(()=>{
			//pound
			let image = new Image();
			image.src = 'data:image/png;base64,';
			return image;
		})(),
		(()=>{
			//yen
			let image = new Image();
			image.src = 'data:image/png;base64,';
			return image;
		})()
	];
	private width = 0;
	private halfWidth = 0;
	private fourthWidth = 0;
	private eighthWidth = 0;
	private height = 0;
	private isOver = false;
	private startDecay = false;
	private maxCoins = 15;
	private amountOfCoinBeforeDecay = null;

	constructor(
		public viewCtrl: ViewController, 
		public navParams: NavParams
	) {
		this.width = window.innerWidth;
		this.halfWidth = this.width/2;
		this.fourthWidth = this.halfWidth/2;
		this.eighthWidth = this.fourthWidth/2;
		this.height = window.innerHeight;

		this.startConstantLoop();

	}

	ionViewDidLoad() {

		setTimeout(()=>{
			this.fillLoadingBarToHalf();
		},0);
		
		setTimeout(()=>{
			
			this.startDecay = true;
		},8000);
	}

	fillLoadingBarToHalf(){
		this.loadingBarPercentage = 50;
		let loadingBar = this.loadingBar.nativeElement as HTMLElement;
		loadingBar.style.transition = 'all 8s';
		loadingBar.style.transitionTimingFunction = 'linear';
		loadingBar.style.transform = 'scaleX('+ (this.loadingBarPercentage/100) +')';
		
	}

	fillLoadingBarToCustomValue(percentage){



		this.loadingBarPercentage = percentage;
		let loadingBar = this.loadingBar.nativeElement as HTMLElement;
		loadingBar.style.transition = 'all 1s';
		loadingBar.style.transform = 'scaleX('+ (this.loadingBarPercentage/100) +')';
	}

	ngAfterViewInit(){
		this.canvas = this._canvas.nativeElement as HTMLCanvasElement;
		this.canvas.height = window.innerHeight;
		this.canvas.width = window.innerWidth;
		this.context = this.canvas.getContext('2d');
	}

	ngOnDestroy(){
		this.isOver = true;
	}

	ionViewDidEnter(){
		this.startAnimation();
	}

	private _getRandomCoin(){

		var obj = {
			startingPoint  : (this.width / 5) + Math.random() * ( this.width*3/5),
			initDegree  : 45 +  Math.floor(Math.random() * 110),
			rotationSpeed : 1 + Math.floor(Math.random() * 5),
			speed : this.height / (Math.floor(10+Math.random() * 2) * 30)  ,
			x :  - (Math.random() * this.fourthWidth),
			y : 0,
			currentRotation : 0,
			scale : 0.3 + Math.random() / 2,
			isActive : true,
			image : this.images[Math.floor(Math.random() * this.images.length)]
		};

		if(obj.startingPoint >= this.width / 2){
			obj.initDegree = 45 + Math.floor(Math.random()*45)
		}else{
			obj.initDegree = 90 + Math.floor(Math.random()*45)
		}

		return obj;
	}

	private startAnimation(){

		if(this.startDecay == true && this.amountOfCoinBeforeDecay === null){
			this.amountOfCoinBeforeDecay = this.coins.length;
		}

		if(this.startDecay == true && this.coins.length == 0){
			this.prepareNav();
			return;
		}

		if(this.isOver == true){
			return;
		}

		this.context.clearRect(0,0,this.width,this.height);

		for(let i=this.coins.length-1;i>=0;i--){

			let coin = this.coins[i];

			if(coin.isActive == false){
				this.coins.splice(i,1);
				if(this.startDecay == true){
					let coinDecayed =  this.amountOfCoinBeforeDecay - this.coins.length;
					let amountDecayPerHundred = 100 / this.amountOfCoinBeforeDecay;
					let percentShouldIncrement = 40 * amountDecayPerHundred / 100;
					this.fillLoadingBarToCustomValue(this.loadingBarPercentage+percentShouldIncrement);
				}
				continue;
			}

			this.context.save();
			this.context.translate(coin.startingPoint,0);
			this.context.rotate(coin.initDegree * (Math.PI/180));
			this.context.translate(coin.x,0);
			this.context.rotate((coin.currentRotation)* (Math.PI/180));
			this.context.scale(coin.scale, coin.scale);
			this.context.drawImage(coin.image,-this.eighthWidth,-this.eighthWidth,this.fourthWidth,this.fourthWidth);
			this.context.restore();

			coin.x += coin.speed;
			coin.currentRotation += coin.rotationSpeed;

			var tempPosX = Math.sin(Math.abs(coin.initDegree - 90) * Math.PI / 180) * Math.floor(coin.x);
			var tempPosY = Math.cos(Math.abs(coin.initDegree - 90) * Math.PI / 180) * Math.floor(coin.x);

			let widthToCompare = 0;

			if(coin.initDegree > 90){
				widthToCompare = coin.startingPoint;
			}else{
				widthToCompare = this.width - coin.startingPoint;
			}

			
			if(Math.abs(tempPosX) - (this.fourthWidth) > widthToCompare){
				coin.isActive = false;
			}
				

			if(Math.abs(tempPosY) - (this.fourthWidth) > this.height)
				coin.isActive = false;

		}

		requestAnimationFrame(()=>{
			this.startAnimation();
		});
	}

	startConstantLoop(){

		if(this.startDecay == true || this.isOver == true)
			return;

		if(this.coins.length <  this.maxCoins)
			this.coins.push(this._getRandomCoin());

		setTimeout(()=>{
			this.startConstantLoop();
		},500);

	}

	prepareNav(){
		this.fillLoadingBarToCustomValue(100);
		this.navParams.data.prepareForExit().then(()=>{
			this.viewCtrl.dismiss({},'',{
				animation : 'wp-transition'
			});
		});
	}

}



